.hero{
    height: calc(50vh - #{$menu-height});
    position: relative;
    &__background{
        background: url(../images/bg.jpg) no-repeat center;
        background-attachment: fixed;
        width: 100%;
        height: 75%;
        overflow: hidden;

    }
    &__background-img{
        
    }
    &__profile{
        position: absolute;
        bottom: 0;
        height: 25vh;
        width: 25vh;
        border-radius: 50%;
        background-color: green;
        overflow: hidden;

    }
    &__profile-img{
        width: 100%;
        transform: scale(1.4);
        object-position: 50% 0;
    }
}

.presentation{
    text-align: center;
    &__title{
        @include font('principal', 42, $font-semibold, 24, $color-lighter);
       
    }
    &__text{
        @include font('principal', 22, $font-normal, 25, $color-texts);
    }
}