.scene{
    width: 400px;
    height: 400px;
    perspective: 600px;
    .cube{
        width: 100%;
        height: 100%;
        position: relative;
        transform-style: preserve-3d;
        transform: translateZ(-200px);
        will-change: transform;
        transition: transform 1s ease-in-out;
        %cube__face{
            width: 400px;
            height: 400px;
            position: absolute;
            display: flex;
            justify-content: center;
            align-items: center;
            -webkit-user-select: none;
            -khtml-user-select: none;
            -moz-user-select: none;
            -ms-user-select: none;
            user-select: none;
            cursor: pointer;
            @include font('principal', 62, $font-semibold, 62, $color-white);
            border: 10px solid $color-black;
            text-shadow: 0 0 20px $color-black;
        }
        &__face--front{
            @extend %cube__face;
            background-color: rgba($color: $color-js, $alpha: .95);
            transform: rotateY(0) translateZ(200px);
        }
        &__face--back{
            @extend %cube__face;
            background-color: rgba($color: $color-css, $alpha: .95);
            transform: rotateY(180deg) translateZ(200px);
        }
        &__face--right{
            @extend %cube__face;
            background-color: rgba($color: $color-html, $alpha: .95);
            transform: rotateY(90deg) translateZ(200px);
        }
        &__face--left{
            @extend %cube__face;
            background-color: rgba($color: $color-sass, $alpha: .95);
            transform: rotateY(-90deg) translateZ(200px);
        }
        &__face--top{
            @extend %cube__face;
            background-color: rgba($color: $color-git, $alpha: .95);
            transform: rotateX(90deg) translateZ(200px);
        }
        &__face--bottom{
            @extend %cube__face;
            background-color: rgba($color: $color-jquery, $alpha: .95);
            transform: rotateX(-90deg) translateZ(200px);
        }
    }
}
